.app {
    padding: 20px;
    width: 600px;
    text-align: center;
    margin: 30px;
    background-color: antiquewhite;
    box-shadow: 0px 0px 0px 5px #A0A0A0, inset 0px 10px 27px -8px #141414, inset 0px -10px 27px -8px #A31925, 5px 5px 15px 5px rgba(0, 0, 0, 0);
    
}


.affair {
    background-color: burlywood;
    border: 1px solid gray;
    border-radius: 10px;
    margin: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.affairPart {
    width: 25%;
    padding: 4px;
}

.affair:last-child {
    display: inline-block;
    width: 25%;

}


.buttons {
    margin: 30px 10px 0 10px;
    padding: 5px;
    border-radius: 10px;
    text-decoration: none;
    border: solid 1px gray;
    background-color: bisque;
}

.btn {
    border-radius: 50%;
    text-decoration: none;
    border: solid 1px gray;
    background-color: antiquewhite;
}

.red {
    width: 25%;
    padding: 4px;

    background: rgb(2, 0, 36);
    background: linear-gradient(90deg, rgba(2, 0, 36, 0) 0%, rgba(255, 0, 0, 1) 50%, rgba(0, 212, 255, 0) 100%);
}

.yellow {
    width: 25%;
    padding: 4px;

    background: rgb(2, 0, 36);
    background: linear-gradient(90deg, rgba(2, 0, 36, 0) 0%, rgba(255, 248, 0, 1) 50%, rgba(0, 212, 255, 0) 100%);
}

.green {
    width: 25%;
    padding: 4px;

    background: rgb(2, 0, 36);
    background: linear-gradient(90deg, rgba(2, 0, 36, 0) 0%, rgba(0, 255, 8, 1) 50%, rgba(0, 212, 255, 0) 100%);
}
